import * as React from 'react';
import { Route } from 'react-router-dom';
import Navigation from './components/navigation/Navigation';
import Footer from './components/footer/Footer';
import GridCollection from './components/collection/GridCollection';
import VerticalCollection from './components/collection/VerticalCollection';
import SubtitledPicture from './components/picture/SubtitledPicture';
import './App.css';

class PictureGrid extends React.Component {
  render() {
    return (
      <GridCollection>
        <SubtitledPicture />
	<SubtitledPicture />
	<SubtitledPicture />
        <SubtitledPicture />
	<SubtitledPicture />
	<SubtitledPicture />
      </GridCollection>
    );
  }
}

class PostList extends React.Component {
  render() {
    return (
      <VerticalCollection>
        <div className="post"><p>EXEMPLO DE POSTAGEM</p></div>
        <div className="post"><p>EXEMPLO DE POSTAGEM</p></div>
	<div className="post"><p>EXEMPLO DE POSTAGEM</p></div>
	<div className="post"><p>EXEMPLO DE POSTAGEM</p></div>
      </VerticalCollection>
    );
  }
}

class App extends React.Component {
  render() {
    return (
      <div className="App">
        <div className="App-header">
          <h2>João Braga</h2>
        </div>
        <Navigation />
        <Route exact path="/site-joao-braga/" render={() => <p>INÍCIO</p>} />
        <Route path="/site-joao-braga/blog" component={PostList} />
        <Route path="/site-joao-braga/palestras" component={PostList} />
        <Route path="/site-joao-braga/cursos" component={PictureGrid} />
        <Route path="/site-joao-braga/livros" component={PictureGrid} />
        <Route path="/site-joao-braga/viagens" component={PictureGrid} />
        <Route path="/site-joao-braga/na-midia" component={PostList} />
        <Route path="/site-joao-braga/agenda" component={PostList} />
        <Route path="/site-joao-braga/alunos" component={PictureGrid} />
        <Footer />  
      </div>
    );
  }
}

export default App;



