import * as React from 'react';
import {
  Navbar,
  Nav,
  NavItem,
} from 'react-bootstrap';
import './Navigation.css';

class Navigation extends React.Component {
  render() {
    return (
      <Navbar>
        <Nav>
          <NavItem eventKey={0} href="/site-joao-braga/blog">TEXTOS</NavItem>
          <NavItem eventKey={1} href="/site-joao-braga/palestras">PALESTRAS</NavItem>
          <NavItem eventKey={2} href="/site-joao-braga/cursos">CURSOS</NavItem>
          <NavItem eventKey={3} href="/site-joao-braga/livros">LIVROS</NavItem>
          <NavItem eventKey={4} href="/site-joao-braga/viagens">VIAGENS</NavItem>
          <NavItem eventKey={5} href="/site-joao-braga/na-midia">NA MÍDIA</NavItem>
          <NavItem eventKey={6} href="/site-joao-braga/agenda">AGENDA</NavItem>
          <NavItem eventKey={7} href="/site-joao-braga/alunos">ESPAÇO DO ALUNO</NavItem>
        </Nav>
      </Navbar>
    );
  }
}

export default Navigation;



