import * as React from 'react';

class SubtitledPicture extends React.Component {
  render() {
    return (
      <div {...this.props}>
        <figure>
	  <img src={require('./placeholders/placeholder-300x300.jpeg')} />
     	  <figcaption>LEGENDA</figcaption>
        </figure>
      </div>
    );
  }
}

export default SubtitledPicture;



